import React from 'react';
import logo from './logo.svg';
import './App.css';
import {Workbox, messageSW} from 'workbox-window';
import { BroadcastUpdatePlugin } from 'workbox-broadcast-update';
const wb = new Workbox('/service-worker.js');      
var registration;
function App() {

  var sn = document.getElementById("snackbar");

  if(process.env.NODE_ENV === 'production' && 'serviceWorker' in navigator)
  {
    
   

    wb.addEventListener('waiting', showSkipWaitingPrompt);
    wb.addEventListener('externalwaiting', showSkipWaitingPrompt);
    wb.register().then((r) => registration = r);
  }
 
  const showSkipWaitingPrompt = (event) => {
    
    //update = () => {
      //sn.className = sn.className.replace("show", ""); 
      wb.addEventListener('controlling', (event) => {
        window.location.reload();
        });
        if (registration && registration.waiting) {
        messageSW(registration.waiting, {type: 'SKIP_WAITING'});
    }
  //}
  } 

  
  const hideSnackbar = () => {
    sn.className = "";
    //setTimeout(function(){ sn.className = sn.className.replace("show", ""); }, 10000);
  } 

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Latest Application v5
        </a>
      </header>
      <div id="snackbar">An Update is Available <button onClick={()=>{hideSnackbar()}}>Update</button></div>
    </div>
  );
}

export default App;
